var Useragent = require("useragent");

import Board from './board';

var Tools = require("../helpers/tools");

var board = new Board("s", "Софт и железо");

board.extraScripts = function() {
    return [ { fileName: "s.js" } ];
};

board.postExtraData = function(req, fields, _, oldPost) {
    if (oldPost)
        return Promise.resolve(oldPost.extraData);
    var ua = req.headers["user-agent"] || null;
    if (!ua)
        return Promise.resolve(null);
    ua = (Useragent.parse(ua).toString() || "").replace(/ 0\.0\.0$/, "");
    return Promise.resolve(ua || null);
};

module.exports = board;
